*, *::before, *::after { box-sizing: border-box; }

html { box-sizing: border-box; }

html, body { margin: 0; padding: 0; }

body { display: flex; flex-direction: column; width: 100%; height: 100%; font-style: normal; font-weight: 400; font-size: 16px; line-height: 24px; font-family: "Comfortaa", cursive; color: #121212; background-color: #ffffff; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

a { color: #3c2a47; text-decoration: none; }

textarea { resize: none; }

input:-webkit-autofill { box-shadow: inset 0 0 0 1000px #ffffff; -webkit-text-fill-color: #121212; }

input, textarea { border-radius: 0; }

input::placeholder, textarea::placeholder { opacity: 1; }

input:invalid, textarea:invalid { box-shadow: none; }

select { border-radius: 0; }

input::-ms-clear, input::-ms-reveal { display: none; }

input[type="search"]::-webkit-search-decoration, input[type="search"]::-webkit-search-cancel-button, input[type="search"]::-webkit-search-results-button, input[type="search"]::-webkit-search-results-decoration { appearance: none; }

input::-webkit-outer-spin-button, input::-webkit-inner-spin-button { margin: 0; appearance: none; }

input[type="number"] { appearance: textfield; }

select, textarea, input:matches([type="email"], [type="number"], [type="password"], [type="search"], [type="tel"], [type="text"], [type="url"]) { appearance: none; }

button, [type="button"], [type="reset"], [type="submit"] { appearance: none; }

.container { display: flex; justify-content: center; align-items: center; background-color: #ffffff; font-size: 16px; }

.header { display: flex; justify-content: space-between; }

h1 { font-size: 1.5rem; margin: 20px 0 0; }

.volume { padding: 9px 2px 5px; margin: 15px 15px 15px 0; background-color: #ffffff; border: 1px solid #ffffff; border-radius: 4px; box-shadow: 1px 0px 6px rgba(0, 0, 0, 0.2); cursor: pointer; }

main { display: block; flex-grow: 1; }

.box-copyright { display: flex; justify-content: flex-end; margin-top: 42px; }

.box-copyright__copy { margin: 25px 20px 0; }

.toggle-switch { background: #f3f3d3; width: 80px; height: 30px; overflow: hidden; border-radius: 3px; display: inline-block; vertical-align: middle; margin: 0 10px; }

.toggle-switch:after { content: " "; display: block; width: 40px; height: 30px; background-color: #3498db; border: 3px solid #ffffff; border-top: 0; border-bottom: 0; margin-left: -3px; transition: all 0.1s ease-in-out; }

.active .toggle-switch:after { margin-left: 40px; }

.toggle-label { display: inline-block; line-height: 30px; }

.toggle-label-off { color: #3498db; }

.active .toggle-label-off { color: #121212; }

.active .toggle-label-on { color: #3498db; }

/* скрываем чекбокс */
#menu__toggle { opacity: 0; }

/* стилизуем кнопку */
.menu__btn { display: flex; /* используем flex для центрирования содержимого */ align-items: center; /* центрируем содержимое кнопки */ position: fixed; top: 32px; left: 41px; width: 35px; cursor: pointer; z-index: 15; }

/* добавляем "гамбургер" */
.menu__btn > span, .menu__btn > span::before, .menu__btn > span::after { display: block; position: absolute; width: 100%; height: 4px; background-color: #3c2a47; transition: 0.5s ease; }

.menu__btn > span::before { content: ""; top: -8px; }

.menu__btn > span::after { content: ""; top: 8px; }

/* контейнер меню */
.menu__box { display: block; position: fixed; visibility: hidden; top: 0; left: -100%; width: 300px; height: 100%; margin: 0; padding: 80px 0; list-style: none; text-align: center; background-color: #f3f3d3; box-shadow: 1px 0px 6px rgba(0, 0, 0, 0.2); z-index: 9; transition: 0.5s ease; }

/* элементы меню */
.menu__item { display: flex; padding: 15px 0 15px 51px; color: #121212; font-size: 20px; font-weight: 300; text-decoration: none; transition: 0.5s ease; }

.menu__item:hover { background-color: #cfd8dc; }

#menu__toggle:checked ~ .menu__btn > span { transform: rotate(45deg); left: 191px; }

#menu__toggle:checked ~ .menu__btn > span::before { top: 0; transform: rotate(0); }

#menu__toggle:checked ~ .menu__btn > span::after { top: 0; transform: rotate(90deg); }

#menu__toggle:checked ~ .menu__box { visibility: visible; left: 0; }

.piano { width: 580px; height: 209px; margin: 130px auto; display: flex; justify-content: space-between; }

.key { width: 118px; height: 100%; border: 2px solid #ffffff; border-radius: 0 0 5px 5px; cursor: pointer; text-align: center; font-size: 40px; font-family: sans-serif; font-weight: bold; background: #d9d0ac; user-select: none; transition: 0.1s; display: flex; align-items: flex-end; justify-content: center; }

.key-b { width: 70px; background: #3c2a47; font-size: 30px; color: #ffffff; height: 60%; margin-left: -23px; margin-right: -26px; z-index: 2; }

.key.active { background: #f3f3d3; }

.key-b.active { background: #3c2a47; }
